import { Button, Form, Select } from 'antd'
import React from 'react'

const Index = () => {
    return (
        <>
            <Form
                layout="vertical"
            >
                <Form.Item label="Огноо">
                    <Select></Select>
                </Form.Item>
                <Form.Item>
                    <Button type="primary">Журнал харах</Button>
                </Form.Item>
            </Form>
        </>
    )
}

export default Index